import { memo } from "react";
import { NodeResizer } from "reactflow";

const NodeSelectedGroup = ({ data, selected }) => {
  return (
    <>
      <NodeResizer isVisible={selected} minWidth={100} minHeight={50} />
      <p
        style={{
          fontWeight: "bold",
          fontSize: "15px",
          textAlign: "center",
          verticalAlign: "middle",
          alignItems: "center",
          maxWidth: "140px",
          minHeight: "80%",
          margin: "0px",
          padding: "10px",
          borderRadius: "10px",
          color: "white",
          backgroundColor: data.color,
        }}
      >
        {data.label}
      </p>
      <div
        style={{
          padding: "7px",
          borderRadius: "10px",
        }}
      ></div>
    </>
  );
};

export default memo(NodeSelectedGroup);
