import React, { useState, useEffect } from "react";
import { useStoreApi, useReactFlow, Panel } from "reactflow";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";

const panelStyle = {
  color: "#777",
  fontSize: 12,
  backgroundColor: "#fff", // Set a background color
};

const selectStyle = {
  fontSize: 12,
  marginRight: 5,
  marginTop: 5,
};

export default ({ departments, onDepartmentClick }) => {
  const store = useStoreApi();
  const { setCenter } = useReactFlow();
  const [selectedDepartment, setSelectedDepartment] = useState("");

  useEffect(() => {
    // Set the default selected value to the first department
    if (departments.length > 0) {
      setSelectedDepartment(departments[0]);
    }
  }, [departments]);

  const focusNode = (nodeId) => {
    console.log("Focusing on node with ID:", nodeId);

    const { nodeInternals } = store.getState();
    const node = nodeInternals.get(nodeId);

    if (node) {
      const x = node.position.x + node.width / 2;
      const y = node.position.y + node.height / 2;

      setTimeout(() => {
        setCenter(x, y, { zoom: 1, duration: 1000 });
      }, 0);
    }
  };

  return (
    <Panel position="top-left" style={panelStyle}>
      <Select
        value={selectedDepartment}
        onChange={(e) => {
          const selectedDepartment = e.target.value;
          setSelectedDepartment(selectedDepartment);
          onDepartmentClick(selectedDepartment);
          focusNode(selectedDepartment);
        }}
        style={selectStyle}
      >
        {departments.map((department) => (
          <MenuItem key={department} value={department}>
            {department}
          </MenuItem>
        ))}
      </Select>
    </Panel>
  );
};
